import React, { Component } from "react";
import Card from "react-md/lib/Cards/Card";
import "./Iframe.scss";

class Iframe extends Component {

  componentDidMount() {
    const {
      html,
      onload
    } = this.props;

    if (!html || !this.divRef) return;

    const iframe = document.createElement("iframe");
    iframe.setAttribute("frameborder", "0");
    this.divRef.appendChild(iframe);
  
    iframe.onload = onload || (() => {
      console.log("iframe loaded"); // eslint-disable-line no-console
    });

    const contentDocument = iframe.contentDocument;
    contentDocument.open();
    contentDocument.write(html);
    contentDocument.close();
  }

  render() {
    return (
      <div className="links-component md-grid mobile-fix">
        <Card className="md-grid md-cell--8">
          <div  className="iframe" ref={(x) => { this.divRef = x }} />
        </Card>
      </div>
    );
  }
}

export default Iframe;



