import React, { Component } from "react";
import Helmet from "react-helmet";
import config from "../../data/SiteConfig";
import Iframe from "../components/Iframe/Iframe";

const HTML = '<body><script src="https://gist.github.com/DaisukeSudo/dc2dbe0667879f3a88707d857cc0d84b.js"></script></body>';

class LinksPage extends Component {

  render() {
    return (
      <div className="links-container">
        <Helmet>
          <title>{`Links | ${config.siteTitle}`}</title>
          <link rel="canonical" href={`${config.siteUrl}/links/`} />
        </Helmet>
        <Iframe html={HTML} />
      </div>
    );
  }
}

export default LinksPage;



